// src/pages/About.js
import React, { useEffect, useRef, useState } from "react";
import "../styles/About.css";

/**
 * Pgina informativa 'Acerca de TaskFlow App'
 */
const About = () => {
  const [visible, setVisible] = useState(false);
  const containerRef = useRef();

  // Detecta si la seccin esta visible para activar animaciones
  useEffect(() => {
    const observer = new IntersectionObserver(
      ([entry]) => setVisible(entry.isIntersecting),
      { threshold: 0.1 }
    );
    if (containerRef.current) observer.observe(containerRef.current);
    return () => observer.disconnect();
  }, []);

  return (
    <div className={`about-container ${visible ? "visible" : ""}`} ref={containerRef}>
      <header className="about-header animate-fade-slide delay-0">
        <h1>Acerca de TaskFlow App</h1>
        <p className="subtitle">
          La herramienta perfecta para organizar, priorizar y ejecutar tus tareas como un profesional.
        </p>
      </header>

      <section className="about-card animate-fade-slide delay-1">
        <p>
          TaskFlow App es una aplicacin moderna de gestin de tareas diseada para optimizar tu productividad diaria. Con un enfoque minimalista y funcional, TaskFlow te permite organizar tus proyectos y tareas de manera intuitiva, ayudndote a concentrarte en lo que realmente importa.
        </p>
      </section>

      <section className="about-card animate-fade-slide delay-2">
        <h2> Caractersticas Principales</h2>
        <ul>
          <li>Interfaz minimalista y enfocada en la productividad.</li>
          <li>Tablero Kanban intuitivo para gestionar tareas en tiempo real.</li>
          <li>Diseo responsive adaptable a cualquier dispositivo.</li>
          <li>Almacenamiento seguro de datos en localStorage (mximo 5MB).</li>
          <li>Estructura escalable para futuras integraciones con backend.</li>
        </ul>
      </section>

      <section className="about-card animate-fade-slide delay-3">
        <h2> Tecnologas Utilizadas</h2>
        <p>
          TaskFlow App fue creada con <strong>React</strong> y <strong>React Router</strong>, aplicando hooks modernos,
          animaciones CSS y buenas prcticas de diseo para garantizar un rendimiento ptimo. Toda la experiencia visual
          est impulsada por una paleta elegante de blanco, negro y grises.
        </p>
      </section>

      <section className="about-card animate-fade-slide delay-4">
        <h2> Nuestra Filosofa</h2>
        <p>
          Creemos en la simplicidad y funcionalidad. Diseada para ser intuitiva, TaskFlow App permite que cada usuario se enfoque en sus proyectos sin complicaciones, ofreciendo una base robusta y flexible para crecer junto con sus necesidades.
        </p>
      </section>

      <section className="about-card animate-fade-slide delay-5">
        <h2> Visin Futura</h2>
        <p>
          Comprometidos con la innovacin, En los prximos desarrollos planeamos integrar nuevas funcionalidades:
        </p>
        <ul>
          <li> Sincronizacin en la nube.</li>
          <li> Trabajo colaborativo en tiempo real.</li>
          <li> Opciones de personalizacin avanzadas .</li>
        </ul>
      </section>
    </div>
  );
};

export default About;
