import React from "react";
import "../styles/PoliticasPrivacidad.css";

/**
 * Pgina de Polticas de Privacidad para la aplicacin.
 */
const PoliticasPrivacidad = () => {
  return (
    <div className="politicas-container">
      <h1>Polticas de Privacidad</h1>
      <p className="intro">
        Esta poltica de privacidad describe cmo se recopila, usa y protege la informacin personal que proporcionas al utilizar nuestra aplicacin.
      </p>

      <section>
        <h2>1. Informacin que recopilamos</h2>
        <p>
          Podemos recopilar informacin personal como tu nombre, direccin de correo electrnico, y cualquier dato que proporciones al registrarte, crear tableros, tareas o interactuar con la aplicacin.
        </p>
      </section>

      <section>
        <h2>2. Uso de la informacin</h2>
        <p>
          La informacin recopilada se utiliza exclusivamente para:
        </p>
        <ul>
          <li>Proporcionar y mejorar nuestros servicios.</li>
          <li>Personalizar tu experiencia en la aplicacin.</li>
          <li>Comunicarnos contigo cuando sea necesario.</li>
        </ul>
      </section>

      <section>
        <h2>3. Almacenamiento de datos</h2>
        <p>
          La informacin se almacena localmente en tu navegador (localStorage) y no se comparte con terceros. Si en el futuro implementamos un servidor o base de datos, se actualizar esta poltica.
        </p>
      </section>

      <section>
        <h2>4. Seguridad</h2>
        <p>
          Nos comprometemos a proteger tu informacin. Aunque trabajamos para mantener medidas de seguridad adecuadas, recuerda que ninguna transmisin de datos por internet o sistema de almacenamiento es completamente seguro.
        </p>
      </section>

      <section>
        <h2>5. Cookies</h2>
        <p>
          Esta aplicacin no utiliza cookies de terceros ni tecnologas de rastreo. Toda la informacin es gestionada de manera local.
        </p>
      </section>

      <section>
        <h2>6. Derechos del usuario</h2>
        <p>
          Puedes modificar o eliminar tu informacin personal directamente desde la aplicacin. Si deseas eliminar todos tus datos, puedes hacerlo desde tu menu de usuario. Ten en cuenta que esto eliminar toda la informacin almacenada localmente.
        </p>
      </section>

      <section>
        <h2>7. Cambios en la poltica</h2>
        <p>
          Esta poltica puede ser actualizada en cualquier momento. Te notificaremos dentro de la app sobre cualquier cambio relevante.
        </p>
      </section>

      <section>
        <h2>8. Contacto</h2>
        <p>
          Si tienes preguntas o inquietudes sobre esta poltica de privacidad, puedes escribirnos al correo: <a href="mailto:soporte@app.com">soporte@app.com</a>.
        </p>
      </section>
    </div>
  );
};

export default PoliticasPrivacidad;
